/* General Configuration */

* {
  /*background-color: #E9B44C;*/
  color: #1C110A;
  font-family: arial;
}

html {
  scroll-behavior: smooth;
}

h1 {
  font-size: 50px;
}

p {
  color: grey
}

.intro {
  display: flex;
  align-items: center;
  /*justify-content: center;*/
  height: 100vh;
  padding: 20px;

}

.introBox {
  margin-left: 25%;
  margin-right: 25%;
}

.question {
  display: flex;
  align-items: center;

  height: 100vh;
  padding: 20px;
}

.questionBox {
  margin-left: 25%;
  margin-right: 25%;
}

.questionTitle {
  font-size: 25px;
  margin-bottom: 10px;
}

.results {
  display: flex;
  align-items: center;
  height: 100vh;
  padding: 20px;
}

.resultsBox {
  margin-left: 25%;
  margin-right: 25%;
}

.fa-check {
  color: green;
}

.fa-times {
  color: red;
}
